.pools-table {
  display: flex;
  flex-direction: column;

  --default-collumns: 30px 1.5fr repeat(3, 1fr) 0.7fr 0.5fr;



  &.tokens {
    @include tablet {
      --default-collumns: 30px 1.5fr 1fr 1fr 0.5fr;
    }

    @include small-tablet {
      --default-collumns: 30px 1.5fr 1fr .8fr;
    }

    @include mobile {
      --default-collumns: 1fr .6fr;
    }


    .pools-table__head-title,
    .pools-table__list-value  {
      @include tablet {
        &:nth-child(4),
        &:nth-child(6) {
          display: none;
        }
      }

      @include small-tablet {

        &:nth-child(7) {
          display: none;
        }
      }

      @include mobile {
        &:nth-child(1),
        &:nth-child(3) {
          display: none;
        }
      }
    }

  }

  &:not(.tokens) {

    @include tablet {
      --default-collumns: 30px 1.5fr 1fr 1fr 0.5fr;
    }

    @include small-tablet {
      --default-collumns: 30px 1.5fr .8fr;
    }

    @include mobile {
      --default-collumns: 1fr .4fr;
    }

    .pools-table__head-title,
    .pools-table__list-value {
      @include tablet {
        &:nth-child(4),
        &:nth-child(5) {
          display: none;
        }
      }

      @include small-tablet {
        &:nth-child(6),
        &:nth-child(7) {
          display: none;
        }
      }

      @include mobile {
        &:nth-child(1) {
          display: none;
        }
      }
    }

  }

  &__head {
    padding: 0px clamp(15px, 2.308vw, 30px) 30px;
    display: grid;
    grid-template-columns: var(--default-collumns);
    grid-gap: 20px;

    &-title {
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 100%;
      color: rgba(var(--cl-f), 1);

      &:last-child {
        text-align: right;
      }

    }

  }

  &__list {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;

    @include small-tablet {
      grid-gap: clamp(20px, 3.077vw, 40px);
    }

    &-inner {
      display: flex;
      align-items: center;
      grid-gap: clamp(5px, 1.709vw, 20px);
      width: 100%;
      font-style: normal;
      font-family: var(--font-family2);
      font-weight: 500;
      font-size: 16px;
      line-height: 100%;
      color: rgba(var(--cl-a), 1);

      b {
        font-family: var(--font-family2);
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
        color: rgba(var(--cl-f), 1);

        @include small-tablet {
          display: none;
        }
      }

      &--mode {
        grid-gap: 10px;
      }
    }

    &-value {
      font-style: normal;
      font-family: var(--font-family2);
      font-weight: 500;
      font-size: 16px;
      line-height: 100%;
      color: rgba(var(--cl-a), 1);

      &:last-child {
        text-align: end;
      }

      svg {
        fill: transparent;
      }
    }

    &-row {
      padding: 16px clamp(15px, 2.308vw, 30px);
      display: grid;
      grid-template-columns: var(--default-collumns);
      grid-gap: 14px;
      border-radius: 12px;
      align-items: center;
      transition: all 1s;

      &:hover {
        background: linear-gradient(
          90deg,
          #24295e 0%,
          rgba(36, 41, 94, 0) 108.36%
        );
      }

    }
  }

  .pagination {
    display: flex;
    flex-direction: row;

    @include small-tablet {
      margin: 14px auto 0;
    }
  }
}
