.modal-wait{
    max-width: 373px;
    width: 90vw;
    padding:
    clamp(13px, 3.162vw, 30px)
    clamp(25px, 3.162vw, 32px)
    clamp(37px, 6.325vw, 60px);
    background: rgba(var(--bg-p),1.0);
    border-radius: 20px;

    &__box{
        display: flex;
        flex-direction: column;
        grid-gap: 20px;

        p{
            max-width: 400px;
            font-family: var(--font-family);
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            text-align: center;
            color: rgba(var(--cl-o),1.0);
        }
    }
    &__title{
        font-family:  var(--font-family2);
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 32px;
        color: rgba(var(--cl-a),1.0);
        width: 100%;
        text-align: center;
        padding-bottom: 10px;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__subtitle{
        font-family:  var(--font-family2);
        font-style: normal;
        font-weight: 600;
        font-size: 21px;
        line-height: 27px;
        color: rgba(var(--cl-a),1.0);
        text-align: center;
    }
    &__inner{
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 10px;
    }

    &__img {
        display: flex;
        justify-content: center;
        width: 122px;
        height: 122px;
        position: relative;
        margin: 0 auto;

        & > * {
          width: 120%;
          height: 120%;
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-60%, -50%) rotate(28deg) scale(1.2) !important;

        }

    }
}
