.main-graffic {
  position: relative;

  display: flex;
  flex-direction: column;
  // min-height: clamp(334px, 37.436vw, 438px);

  background-color: rgba(var(--bm-e),1.0);
  border-radius: 12px;

  &::before {
    content: "";
    position: absolute;
    z-index: 0;

    background: linear-gradient(154.37deg, rgba(104, 102, 246, 0.35) 18.97%, rgba(104, 102, 246, 0) 82.76%);
    border-radius: 12px;

    inset: 0;
  }

  &__pic {
    height: 100%;
    padding: 0 30px 30px;
  }



  &__box {
    z-index: 2;

    display: flex;
    flex-direction: column;
    padding: clamp(20px, 3.077vw, 36px) clamp(20px, 2.991vw, 35px) 0;

    grid-gap: clamp(10px, 1.282vw, 15px);
  }

  &__name {
    font-style: normal;
    font-weight: 600;
    font-size: clamp(20px, 1.795vw, 21px);
    line-height: 100%;
    font-family: var(--font-family3);
    color: rgba(var(--cl-f),1.0);
  }

  &__value {
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 32px;
    font-family: var(--font-family2);
    color: rgba(var(--cl-a),1.0);
  }

  &__date {
    font-style: normal;
    font-weight: 600;
    font-size: clamp(14px, 1.368vw, 16px);
    line-height: 120%;
    font-family: var(--font-family3);
    color: rgba(var(--cl-f),1.0);
  }
}


.token-liquidity__pic,
.main-graffic__pic {
  position: relative;
  overflow: hidden;

  > *:not(.main-graffic__preload-icon) {
    transition: all .4s;
    opacity: 0;
  }

  &.loaded {
    > * {
      opacity: 1;
    }
  }
}

.main-graffic__preload {
  opacity: 1 !important;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 20;
  top: 0;
  left: 0;
  transition: all .5s;

  svg {
    width: calc(100% - (clamp(20px, 2.991vw, 35px) * 2)) !important;
    height: auto !important;
  }

  &.active {
    transform: translateX(100%);
  }
}


.token-liquidity__pic  {


  .main-graffic__preload {
    svg {
      width: 100%!important;
      object-fit: cover;
      height: auto !important;
    }
  }


}
