@mixin overlay {
  position: fixed;
  height: -webkit-fill-available;
  min-height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 20px;
  overflow-y: scroll;
  visibility: hidden;
  opacity: 0;
  background:
  rgba(8, 20, 40, 0.87) linear-gradient(180deg, rgba(104, 102, 246, 0) 0%, rgba(104, 102, 246, 0.21) 100%);
  transition: all .6s;
  transition-delay: 0.5s;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;

  pointer-events: none;

  &::-webkit-scrollbar {
    display: none;
  }

  .modal {
    transform: none !important;
    position: fixed;
    opacity: 0;
    left: initial;
    transform: none;
    visibility: hidden;
    opacity: 0;
    transition: opacity .4s;
  }

  &.active {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.6s;

    pointer-events: initial;

    .modal.active {
      position: relative;
      transform: none;
      visibility: visible;
      opacity: 1;
      -webkit-transition: 1s;
      transition: 1s;
      pointer-events: initial
    }

  }
}
