footer {
  background-color: rgba(var(--bg-k),1.0);
  padding-top: clamp(40px, 5.462vw, 65px);

  @include mobile {
    padding: 40px 0 60px;
  }
}

.footer-inner {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(60px, 6.723vw, 80px);
}

.footer-bottom {
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: center;

  @include mobile {
    padding: 0;
    flex-direction: column;
    grid-gap: 20px;
  }

  &::before {
    height: 1px;
    content: '';
    position: absolute;
    width:  clamp(100%, 80vw, 80vw);
    top: 0;
    background: var(--br-d);
    left: 50%;
    transform: translateX(-50%);

    @include mobile {
      content: none;
    }
  }

  &__info {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 1.345vw, 16px);
    line-height: 100%;
    color: rgba(var(--cl-j),1.0);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-gap: 40px;
  }

  &__link {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 1.345vw, 16px);
    line-height: 100%;
    color: rgba(var(--cl-j),1.0);
    transition: all .4s;

    &:hover {
      color: rgba(var(--cl-a),1.0);
    }
  }

  &__item:first-child {
    position: relative;

    &::before {
      width: 1px;
      height: 100%;
      content: '';
      position: absolute;
      right: -20px;
      transform: translateX(-50%);
      background: var(--br-d);
    }
  }
}


.footer {
  display: grid;
  grid-template-columns: auto 1fr 1fr;
  align-items: flex-start;
  grid-gap: Min(5.723vw, 80px);


  @include tablet {
    grid-template-columns: repeat(3, auto);
    justify-content: space-between;
    grid-gap: 15px;
  }

  @include small-tablet {
    grid-template-columns: 40% 1fr;
    grid-gap: Min(5.723vw, 80px);
  }

  @include mobile {
    grid-template-columns: 100%;
    grid-gap: 40px;
  }

  &__logo {

    @include mobile {
      width: 213px;

      svg {
        width: 100%;
        height: auto;
      }
    }
  }

  &__coin{
    max-width: 387px;
    width: 100%;
    background: linear-gradient(180deg, #0E1E3C 0%, rgba(14, 30, 60, 0) 100%);
    border-radius: 8px;
    padding: clamp(15px, 2.564vw, 30px) clamp(10px, 2.137vw, 25px);
    display: flex;
    justify-content: space-between;
    grid-gap: 10px;

    @include mobile{
      justify-self: center;
      max-width: 100%;
    }

    @include ios{
      flex-direction: column;
   }
    &-box{
      display: flex;
      flex-direction: column;
      grid-gap: 18px;

      @include ios{
        display: grid;
        grid-template-columns: 1fr 1fr;

        .main-button{
          justify-self: end;
        }
      }
    }

    &-wrapper{
      display: grid;
      grid-template-columns: auto auto;
      grid-gap: 2px 10px;

      @include ios{
        grid-template-columns: 35px auto;
      }
    }

    &-pic{
      grid-row: span 2;
    }

    &-name{
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      color: rgba(var(--cl-f),1.0);
    }

    &-value{
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      color: rgba(var(--cl-o),1.0);
    }
  }

  &__form {
    max-width: 354px;

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__social {
    @include tablet {
      grid-template-columns: repeat(3, 1fr);
    }

    @include small-tablet {
      display: flex;
      flex-wrap: wrap;
    }

    @include mobile {
      justify-content: center;
    }

    @include ios{
      width: 100%;
      padding: 0 15px;
      justify-content: space-between;
    }
  }

  &__coll {
    display: flex;
    flex-direction: column;
    grid-gap: clamp(24px, 2.521vw, 30px);

    @include mobile {
      align-items: center;
    }
  }

  &__nav {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: Min(2.017vw, 24px) Min(5.294vw,63px);
    @include laptop{
      grid-gap: 15px 30px;
    }
    @include tablet {
      grid-template-columns: repeat(2, auto);
      grid-gap: 15px 30px;
    }

    @include small-tablet {
      display: none;
    }

    &-link {
      font-family: var(--font-family);
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 100%;
      color: rgba(var(--cl-j),1.0);
      transition: color .2s;
      white-space: nowrap;
      &:hover {
        color: rgba(var(--cl-a),1.0);
      }
    }
  }

  &__label {
    padding: 13px 20px;
    background: rgba(var(--bg-m),1.0);
    border-radius: 6px;
    width: fit-content;

    @include mobile {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: -20px;

    }
  }


}
