  height: 13px;
  width: 13px;
  border-radius: 50%;
  background: rgba(var(--bg-y), 1);
  transition: background 0.3s ease-in-out;
}
.custom-range__counter {
  font-family: var(--font-family2);
  font-style: normal;
  font-weight: 500;
  font-size: 21px;
  line-height: 100%;
  color: rgba(var(--cl-a), 1);
}

html {
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;
}

body {
  position: relative;
  overflow-x: inherit;
  width: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: var(--font-family);
  color: rgba(var(--color-b), 1);
  background: var(--body-bg);
  -webkit-text-size-adjust: 100%;
}
body.fixed-body {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}

main {
  padding-top: var(--header-height);
  transition: padding-top 0.5s linear;
  min-width: 0 !important;
  position: relative;
  z-index: 2;
}

.round-bg {
  position: relative;
  z-index: 0;
}
.round-bg::before, .round-bg:after {
  content: "";
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  z-index: -1;
  pointer-events: none;
}
.round-bg::before {
  background-image: var(--round-right);
  width: clamp(233px, 27.227vw, 324px);
  height: clamp(218px, 17.311vw, 206px);
  right: 0%;
  top: 50%;
  transform: translate(80%, -50%);
  filter: blur(100px);
  opacity: 0.6;
}
.round-bg::after {
  background-image: var(--round-left);
  width: clamp(327px, 32.437vw, 386px);
  height: clamp(301px, 29.916vw, 356px);
  left: 0;
  top: 50%;
  transform: translate(-90%, -50%);
  filter: blur(10px);
  opacity: 0.3;
}
@media (max-width: 1239px) {
  .round-bg::after {
    transform: translate(-60%, -50%);
  }
}

.has-bg .container:nth-child(1) {
  padding-top: clamp(46px, 6.05vw, 75px);
  padding-bottom: clamp(46px, 6.05vw, 59px);
  background-image: url(../img/logo-bg.svg);
  background-position: 81% center;
  background-repeat: no-repeat;
  background-size: clamp(126px, 19.748vw, 235px);
}
@media (max-width: 576px) {
  .has-bg .container:nth-child(1) {
    background-position: 100% top;
  }
}

.dark-section {
  background: rgba(var(--bg-n), 1);
  position: relative;
  z-index: 0;
}
.dark-section::before, .dark-section:after {
  content: "";
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
  pointer-events: none;
  width: 313px;
  height: 984px;
  top: 50%;
  transform: translateY(-50%);
}
.dark-section::before {
  background-image: var(--dark-left);
  left: 0;
  background-position: left;
}
.dark-section::after {
  transform: scaleX(-1) translateY(-50%);
  right: 0;
  background-image: var(--dark-left);
  background-position: right;
}

.mode-section {
  position: relative;
  z-index: 0;
}
.mode-section::before, .mode-section:after {
  content: "";
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
  pointer-events: none;
  width: 452px;
  height: 452px;
  top: 50%;
  transform: translateY(-50%);
  background: #6866F6;
  filter: blur(132.5px);
}
.mode-section::before {
  left: 0;
  transform: translate(-100%, -50%);
}
.mode-section::after {
  right: 0;
  transform: translate(100%, -50%);
}
.mode-section--custom::before, .mode-section--custom:after {
  top: 80%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  margin: 0;
  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
a svg {
  pointer-events: none;
}

button {
  display: inline-flex;
  margin: 0;
  padding: 0;
  line-height: initial;
  color: inherit;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
button svg, button img {
  pointer-events: none;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

label {
  display: inline-flex;
}

input {
  width: 100%;
  background-color: none;
  border: none;
  outline: none;
}
input::placeholder {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

textarea {
  border: none;
  outline: none;
  resize: none;
}

input[type=number] {
  appearance: textfield;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  position: relative;
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.panel,
[data-content] {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

[data-tab-content], [data-inner-content], [data-small-content], [data-wrapper-content] {
  display: none !important;
}
[data-tab-content].active, [data-inner-content].active, [data-small-content].active, [data-wrapper-content].active {
  display: block !important;
}

.dis-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-attachment: fixed;
  overscroll-behavior: none !important;
  scroll-behavior: none !important;
}

.a-start {
  align-items: flex-start !important;
  padding-top: 0 !important;
  height: calc(100% - var(--header-height)) !important;
  top: calc(var(--header-height)) !important;
  transition: none !important;
}

.page--ios .dis-scroll {
  position: relative;
}

.overlay {
  position: fixed;
  height: -webkit-fill-available;
  min-height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 20px;
  overflow-y: scroll;
  visibility: hidden;
  opacity: 0;
  background: rgba(8, 20, 40, 0.87) linear-gradient(180deg, rgba(104, 102, 246, 0) 0%, rgba(104, 102, 246, 0.21) 100%);
  transition: all 0.6s;
  transition-delay: 0.5s;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay .modal {
  transform: none !important;
  position: fixed;
  opacity: 0;
  left: initial;
  transform: none;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.4s;
}
.overlay.active {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.6s;
  pointer-events: initial;
}
.overlay.active .modal.active {
  position: relative;
  transform: none;
  visibility: visible;
  opacity: 1;
  -webkit-transition: 1s;
  transition: 1s;
  pointer-events: initial;
}
.overlay.active--mode .modal:not(.active) {
  overflow: hidden;
  height: 0;
}

.modal[data-tab-content] {
  --calc-padding: calc(clamp(20px, 3.109vw, 37px) + clamp(25px, 4.706vw, 56px));
  margin-top: calc((var(--box-height) + var(--calc-padding) + 20px) * -1);
  z-index: 20;
  position: relative;
}

*::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  border-radius: 5px;
  cursor: pointer;
}
@media (max-width: 767px) {
  *::-webkit-scrollbar {
    display: none;
  }
}
*::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
  transition: all 0.2s;
}
*::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
}
*::-webkit-scrollbar-button {
  display: none;
  background-color: transparent;
  box-shadow: none;
}

.main-preloader {
  z-index: 200;
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  background: rgb(8, 20, 40) linear-gradient(180deg, rgba(104, 102, 246, 0) 0%, rgba(104, 102, 246, 0.21) 100%);
  transform: translateY(-100vh);
  transition: all 0.5s;
}
.main-preloader.active {
  transform: translateY(0vh);
  opacity: 1;
  visibility: visible;
}
@media (max-width: 576px) {
  .main-preloader .main-preloader__image {
    transform: scale(2.5);
  }
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.modal {
  overflow: hidden;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  opacity: 0;
  transition: opacity 1s;
  pointer-events: none;
}
.modal.active {
  position: relative;
  transform: translate(-50%, 0);
  opacity: 1;
  z-index: 20;
  transition: opacity 1s;
  pointer-events: initial;
}
.modal__close {
  position: absolute;
  right: 12px;
  top: 12px;
  z-index: 20;
}
.modal .close svg {
  pointer-events: none;
}

