.wallet-button {
  color: rgba(var(--cl-c), 1);
  width: 168px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 10px;
  background: rgba(var(--bg-e), 1);
  border-radius: 16px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-c), 1);
  transition: all 0.2s;
}
.wallet-button:hover {
  background: rgba(var(--bg-e), 1);
  box-shadow: 0px 6px 10px rgba(104, 102, 246, 0.25);
}
.wallet-button svg {
  stroke: currentColor;
  fill: transparent;
  transition: inherit;
}

.main-button {
  color: rgba(var(--cl-c), 1);
  width: 190px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  background: rgba(var(--bg-e), 1);
  border-radius: 16px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-c), 1);
  transition: all 0.2s;
}
.main-button svg {
  stroke: currentColor;
  fill: transparent;
  transition: inherit;
}
.main-button--transparent {
  background-color: transparent;
  color: rgba(var(--cl-a), 1);
  border: 1px solid var(--br-c);
  border-radius: 16px;
}
.main-button--transparent:hover {
  color: rgba(var(--cl-c), 1);
}
.main-button:hover {
  background: rgba(var(--bg-e), 1);
  box-shadow: 0px 6px 10px rgba(104, 102, 246, 0.25);
  color: rgba(var(--cl-c), 1) !important;
}
.main-button.active {
  background-color: rgba(var(--bg-i), 1);
}
.main-button.active:hover {
  background-color: rgba(var(--bg-i), 1);
}
.main-button--red {
  background-color: rgba(var(--bg-t), 0.5);
  border: 1px solid transparent;
}
.main-button--red:hover {
  background-color: rgba(var(--bg-t), 0.5) !important;
  box-shadow: 0px 6px 10px rgba(var(--bg-t), 0.25);
}
.main-button--red.active {
  background-color: rgba(var(--bg-t), 1);
}
.main-button--red.active:hover {
  background-color: rgba(var(--bg-t), 1);
}
.main-button--mode {
  color: #FFFFFF;
  border-radius: 5px;
  width: 84px;
  height: 40px;
  font-size: 12px;
}

.violet-text {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  color: rgba(var(--cl-e), 1);
}
.violet-text--icon {
  position: relative;
  display: flex;
  align-items: center;
  grid-gap: 1px;
  text-decoration: underline;
  text-underline-offset: 3px;
}

.green-text {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  color: rgba(var(--cl-i), 1) !important;
}

.red-text {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  color: rgba(var(--cl-n), 1);
}

.red-mode-text {
  color: rgba(var(--cl-u), 1);
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

.yellow-text {
  color: rgba(var(--cl-r), 1);
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
}

.transparent-text {
  color: rgba(var(--cl-a), 1);
}

.noUi-target,
.noUi-target * {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -ms-touch-action: none;
  touch-action: none;
  -ms-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.noUi-target {
  position: relative;
}

.noUi-base,
.noUi-connects {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

.noUi-connects {
  overflow: hidden;
  z-index: 0;
}

.noUi-connect,
.noUi-origin {
  will-change: transform;
  position: absolute;
