.modal-submit{
    max-width: 373px;
    width: 90vw;
    padding: clamp(15px, 3.162vw, 30px) clamp(15px, 3.385vw, 44px) clamp(30px, 7.607vw, 60px);
    background: rgba(var(--bg-p),1.0);
    border-radius: 20px;

    &::before{
        position: absolute;
        content: '';
        height: 176px;
        width: 100%;
        background: linear-gradient(180deg, #6866F6 0%, rgba(104, 102, 246, 0) 100%);
        opacity: 0.65;
        top: 0;
        left: 0;
        border-radius: 20px 20px 0px 0px;
    }

    &__box{
        display: flex;
        flex-direction: column;
        grid-gap: 35px;
    }

    &__title{
        font-family:  var(--font-family2);
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 100%;
        color: rgba(var(--cl-a),1.0);
        width: 100%;
        text-align: center;
    }

    &__img {
      margin: -15px 0;
      svg {
        fill: transparent;
      }
    }

    &__top{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__inner{
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 20px;
    }
    &__link{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        text-decoration-line: underline;
        color: rgba(var(--cl-p),1.0);
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        &::before{
            content: '';
            background-image: var(--link-arrow);
            width: 25px;
            height: 25px;
        }
    }
    &__btn{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: rgba(var(--cl-e),1.0);
        border: solid 1px var(--br-c);
        border-radius: 16px;
        min-height: 49px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all .4s;

        &:hover{
            background: rgba(var(--bg-e), 1);
            box-shadow: 0px 6px 10px rgb(104 102 246 / 25%);
            color: rgba(var(--cl-c), 1) !important;
        }

        &.close {
          position: initial;
        }
    }
}
